















































































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Emit,
  Watch
} from "vue-property-decorator";
import { LocaleMessages } from "vue-i18n";
import {
  repositoriesModule,
  routerModule,
  settingsModule,
  adminModule
} from "@/store/modules";
import { Overwrite, EmailNotification } from "@/shared";
import { IJob } from "@/store/modules/JobsModule";
import RepeatPatternInput from "@/components/misc/RepeatPatternInput.vue";
import FileBrowser from "./FileBrowser.vue";
import { defaultRepeatPattern } from "@/misc/cron";
import { parse, addDays } from "date-fns";
import { IRepository } from "@/store/modules/RepositoriesModule";
import { dateToYYYYMMDDString } from "@/components/misc/JobExecutionCalendar.vue";
import equal from "deep-equal";
import debounce from "lodash.debounce";

export type JobOptionalId = Overwrite<
  IJob,
  {
    id?: number;
  }
>;

export const getDefaultJob = (active = true): JobOptionalId => ({
  name: "",
  repoId: 0,
  maxBackups: 100,
  emailNotification: EmailNotification.never,
  backupLocations: [],
  repeatPattern: defaultRepeatPattern,
  active,
  startDate: parse(dateToYYYYMMDDString(new Date()))
});

@Component({
  components: {
    RepeatPatternInput,
    FileBrowser
  }
})
export default class RepositoryEditorComponent extends Mixins() {
  maxNameLength = 100;
  valid = true;
  name = "";
  repoId: number | null = null;
  maxBackups: number | string = 100;
  emailNotification = EmailNotification.never;
  backupLocations: string[] = []; // TODO: add editor
  repeatPattern = defaultRepeatPattern;
  startDate = ""; // TODO: maybe let the user also specify a start time
  active = true;

  debounceEmitInitialData!: () => void;

  @Prop({ type: Boolean, default: false })
  hideSettings!: boolean;
  @Prop({ type: Boolean, default: false })
  hideFiles!: boolean;
  @Prop({ type: Boolean, default: false })
  hideScheduling!: boolean;
  @Prop({ type: Boolean, default: false })
  hideButtons!: boolean;
  @Prop({ type: Boolean, default: false })
  hideRepository!: boolean;

  @Prop({ type: [Array, Boolean], default: false })
  repos!: IRepository[] | false;

  @Prop({ type: [Array, Boolean], default: false })
  clientrepos!: IRepository[] | false;

  @Prop({ type: [String, Boolean], default: false })
  filepath!: string | false;

  @Prop({ type: [String, Boolean], default: false })
  client!: string | false;

  @Prop({
    type: Object,
    default: getDefaultJob
  })
  initialData!: JobOptionalId;

  @Watch("initialData", { immediate: true })
  updateLocalData() {
    this.$nextTick(() => {
      this.name = this.initialData.name;
      this.repoId =
        this.initialData.repoId ||
        Number(routerModule.query.repository) ||
        null; // TODO: maybe update types
      this.maxBackups = this.initialData.maxBackups;
      this.emailNotification = this.initialData.emailNotification;
      this.backupLocations = [...this.initialData.backupLocations];
      this.repeatPattern = this.initialData.repeatPattern;
      this.active = this.initialData.active;
      this.startDate = dateToYYYYMMDDString(this.initialData.startDate);
      if (this.$refs.repeatPatternInput) {
        (this.$refs
          .repeatPatternInput as RepeatPatternInput).clearCurrentRepeatType();
      }
      if (this.$refs.fileBrowser) {
        (this.$refs.fileBrowser as FileBrowser).manuallySetFolders(
          this.initialData.backupLocations
        );
      }
    });
  }

  created() {
    this.debounceEmitInitialData = debounce(
      () => {
        this.$emit("update:initial-data", this.editedData);
      },
      200,
      { trailing: true, maxWait: 3000 }
    );
  }

  get availableRepositories() {
    if (this.clientrepos && this.clientrepos.length) {
      return this.clientrepos.map(r => ({
        text: r.name,
        value: r.id
      }));
    }
    if (this.repos) {
      return this.repos.map(r => ({
        text: r.name,
        value: r.id
      }));
    }
    return repositoriesModule.allRepositories.map(r => ({
      text: r.name,
      value: r.id
    }));
  }

  get availableEmailNotificationItems() {
    return [
      {
        text: this.$t("notification-settings.never"),
        value: EmailNotification.never
      },
      {
        text: this.$t("notification-settings.on-error"),
        value: EmailNotification.onerror
      },
      {
        text: this.$t("notification-settings.always"),
        value: EmailNotification.always
      }
    ];
  }

  /**
   * syncing is used for the job wizard
   */
  @Watch("editedData", { deep: true })
  syncEditedData(cur: JobOptionalId, prev: JobOptionalId) {
    if (!equal(cur, prev)) {
      this.debounceEmitInitialData();
    }
  }

  get editedData(): JobOptionalId {
    return {
      name: this.name,
      repoId: Number(this.repoId),
      maxBackups: Number(this.maxBackups),
      emailNotification: this.emailNotification,
      backupLocations: this.backupLocations,
      repeatPattern: this.repeatPattern,
      id: this.initialData.id,
      active: this.active,
      startDate: parse(this.startDate)
    };
  }

  async submit() {
    if ((this.$refs.form as any).validate()) {
      this.formSubmit(this.editedData);
    }
  }

  @Emit("form-submit")
  formSubmit(payload: JobOptionalId) {}

  async clear() {
    this.$nextTick(() => {
      this.updateLocalData();
    });
  }

  get validateRules(): {
    [idx: string]: ((val: string) => boolean | string | LocaleMessages)[];
  } {
    return {
      repoId: [v => !!v || this.$t("job-editor.repository-required")],
      maxBackups: [
        v => !!v || this.$t("job-editor.max-number-of-backups-required")
      ],
      emailNotification: [
        v => !!v || this.$t("job-editor.email-notification-settings-required")
      ],
      name: [
        v => !!v || this.$t("job-editor.name-required"),
        v =>
          (v && v.length <= this.maxNameLength) ||
          this.$t("job-editor.name-length", {
            length: this.maxNameLength
          })
      ]
    };
  }
}
